import Vue from 'vue'
import Router from 'vue-router'

import Index from '@/pages/index'
import Me from '@/pages/me/me'
import LeaseOrder from '@/pages/order/leaseOrder'
import BuyOrder from '@/pages/order/buyOrder'
import Order from '@/pages/order/order'
import Shop from '@/pages/shop/shopList'
//
import Questionnaire from '@/pages/home/questionnaire'
import Redpacket from '@/pages/home/redpacket'
import Rpfriend from '@/pages/home/rpfriend'
import Goods from '@/pages/home/index_more'
import Extension from '@/pages/home/extension'
import WelfareAgency from '@/pages/home/welfareAgency'
import ProductDetail from '@/pages/home/productDetail'
import WordMouth from '@/pages/home/wordMouth'
import Buy from '@/pages/home/buy'
import Pay from '@/pages/home/pay'
import Agreement from '@/pages/home/agreement'
import Search from '@/pages/home/search'
import PreBuy from '@/pages/home/preBuy'
import FriendBuy from '@/pages/home/friendBuy'
import FriendBuyShare from '@/pages/home/friendBuyShare'
//
import Comments from '@/pages/order/comments'
import OrderDetail from '@/pages/order/orderDetail'
import Relet from '@/pages/order/relet'
import Shopping from '@/pages/order/shopping'
import People from '@/pages/order/people'
import LocationList from '@/pages/order/locationList'
import Refund from '@/pages/order/refund'
import AppointmentExpress from '@/pages/order/appointmentExpress'
import Deny from '@/pages/order/deny'
import Calendar from '@/pages/order/calendar'
import Compensation from '@/pages/order/compensation'
import SendBack from '@/pages/order/sendBack'
import Buypay from '@/pages/order/buypay'
import BuyOrderDetail from '@/pages/order/buyOrderDetail'
import FriendRepay from '@/pages/order/friendRepay'

//
import Rules from '@/pages/me/rules'
import MyInformation from '@/pages/me/myInformation'
import AddInformation from '@/pages/me/addInformation'
import ReceInformation from '@/pages/me/receInformation'
import DetaInformation from '@/pages/me/detaInformation'
import AccountSecurity from '@/pages/me/accountSecurity'
import Coupon from '@/pages/me/coupon'
import Cash from '@/pages/me/cash'
import MyBalance from '@/pages/me/myBalance'
import BankCard from '@/pages/me/bankCard'
import BindingCard from '@/pages/me/bindingCard'
import HelpCenter from '@/pages/me/helpCenter'
import Integral from '@/pages/me/integral'
import Cart from '@/pages/me/cart'
import CommonProblem from '@/pages/me/commonProblem'
import Setup from '@/pages/me/setup'
import AccountBinding from '@/pages/me/accountBinding'
import Modify from '@/pages/me/security/modify'
import Sendcode from '@/pages/me/security/sendcode'
import Newphone from '@/pages/me/security/newphone'
import Newpassword from '@/pages/me/security/newpassword'

//
import Login from '@/pages/login/login'
import PasswordLogin from '@/pages/login/passwordLogin'
import ForgetPassword from '@/pages/login/forgetPassword'
import ResetPassword from '@/pages/login/resetPassword'
import InputCode from '@/pages/login/inputCode'
import AliLogin from '@/pages/login/aliLogin'
import SinaLogin from '@/pages/login/sinaLogin'
import WxLogin from '@/pages/login/wxLogin'

//
import ShopDetail from '@/pages/shop/shopDetail'
import Map from '@/pages/map'

//
import Certification from '@/pages/certification/index'
import School from '@/pages/certification/school'
import cPeople from '@/pages/certification/people'
import AddPeople from '@/pages/certification/addPeople'
import Realname from '@/pages/certification/realname'
import RealnameSuc from '@/pages/certification/realnameSuc'
import RealnameUnbundling from '@/pages/certification/realnameUnbundling'
import Face from '@/pages/certification/face'
//
import Gohosting from '@/pages/hosting/gohosting/index'
import Steps2 from '@/pages/hosting/gohosting/steps2'
import Fittings from '@/pages/hosting/gohosting/fittings'
import Uploadimg from '@/pages/hosting/gohosting/uploadimg'
import Gsuccessful from '@/pages/hosting/gohosting/successful'

import Trusteeship from '@/pages/hosting/myhosting/trusteeship'
import HostDetail from '@/pages/hosting/myhosting/hostDetail'
import HostCancel from '@/pages/hosting/myhosting/hostCancel'
import PlatformDeli from '@/pages/hosting/myhosting/platformDeli'
import PostDeli from '@/pages/hosting/myhosting/postDeli'
import SceneDeli from '@/pages/hosting/myhosting/sceneDeli'
import HostingDetail from '@/pages/hosting/myhosting/hostingDetail'
import HostingExpress from '@/pages/hosting/myhosting/hostingExpress'
import Nearshop from '@/pages/hosting/myhosting/nearshop'
import NotDetail from '@/pages/hosting/myhosting/notDetail'
import NotPay from '@/pages/hosting/myhosting/pay'

//
import Addresslist from '@/pages/address/addresslist'
import Addaddress from '@/pages/address/addaddress'

import NotFound from '@/pages/notFound'

Vue.use(Router)

export default new Router({

  routes: [
    { path: '/', component: Index, meta: { title: '' } },
    { path: '/shop', component: Shop, meta: { title: '', keepAlive: true, isBack: false } },
    { 
      path: '/order', component: Order, meta: { title: '' }, 
      children:[
        { path: '', component: LeaseOrder, meta: { title: '', keepAlive: true, isBack: false  } },
        { path: 'buyOrder', component: BuyOrder, meta: { title: '', keepAlive: true, isBack: false  } },
      ]
    },
    { path: '/me', component: Me, meta: { title: '' } },
    //
    { path: '/comments/:id/:goodid', component: Comments, meta: { title: '' } },
    { path: '/orderDetail/:id', component: OrderDetail, meta: { title: '' } },
    { path: '/relet/:id', component: Relet, meta: { title: '' } },
    { path: '/shopping/:id', component: Shopping, meta: { title: '', keepAlive: true, isBack: false } },
    { path: '/people/:type', component: People, meta: { title: '' } },
    { path: '/locationList/:type', component: LocationList, meta: { title: '' } },
    { path: '/locationList/:type/:id', component: LocationList, meta: { title: '' } },
    { path: '/refund/:id', component: Refund, meta: { title: '' } },
    { path: '/appointmentExpress/:id', component: AppointmentExpress, meta: { title: '' } },
    { path: '/deny/:id', component: Deny, meta: { title: '' } },
    { path: '/calendar/:type', component: Calendar, meta: { title: '' } },
    { path: '/calendar/:type/:type1', component: Calendar, meta: { title: '' } },
    { path: '/compensation/:id', component: Compensation, meta: { title: '' } },
    { path: '/sendBack/:id', component: SendBack, meta: { title: '' } },
    { path: '/buypay/:id', component: Buypay, meta: { title: '' } },
    { path: '/buyOrderDetail/:id', component: BuyOrderDetail, meta: { title: '' } },
    { path: '/friendRepay', component: FriendRepay, meta: { title: '' } },
    //&&
    { path: '/goods/:id', component: Goods, meta: { title: '', keepAlive: true, isBack: false  } },
    { path: '/extension', component: Extension, meta: { title: '' } },
    { path: '/welfareAgency', component: WelfareAgency, meta: { title: '' } },
    { path: '/questionnaire', component: Questionnaire, meta: { title: '' } },
    { path: '/redpacket', component: Redpacket, meta: { title: '' } },
    { path: '/rpfriend', component: Rpfriend, meta: { title: '-' } },
    { path: '/productDetail/:id', component: ProductDetail, meta: { title: 'SKU' } },
    { path: '/wordMouth/:id', component: WordMouth, meta: { title: '' } },
    { path: '/buy', component: Buy, meta: { title: '', keepAlive: true, isBack: false  } },
    { path: '/pay/:orderid', component: Pay, meta: { title: '' } },
    { path: '/zagreement/:title', component: Agreement, meta: { title: '' } },
    { path: '/search', component: Search, meta: { title: '', keepAlive: true, isBack: false } },
    { path: '/preBuy', component: PreBuy, meta: { title: '', keepAlive: true, isBack: false } }, 
    { path: '/friendBuy', component: FriendBuy, meta: { title: '-', keepAlive: true, isBack: false } }, 
    { path: '/friendBuyShare', component: FriendBuyShare, meta: { title: '-' } }, 

    //
    { path: '/rules/:title', component: Rules, meta: { title: '' } },
    { path: '/myInformation', component: MyInformation, meta: { title: '' } },
    { path: '/receInformation', component: ReceInformation, meta: { title: '' } },
    { path: '/addInformation/:id', component: AddInformation, meta: { title: '' } },
    { path: '/addInformation', component: AddInformation, meta: { title: '' } },
    { path: '/detaInformation', component: DetaInformation, meta: { title: '' } },
    { path: '/accountSecurity', component: AccountSecurity, meta: { title: '' } },
    { path: '/modify/:type/:phone', component: Modify, meta: { title: '' } },
    { path: '/coupon', component: Coupon, meta: { title: '' } },
    { path: '/myBalance', component: MyBalance, meta: { title: '' } },
    { path: '/cash', component: Cash, meta: { title: '' } },
    { path: '/bankCard', component: BankCard, meta: { title: '' } },
    { path: '/bindingCard', component: BindingCard, meta: { title: '' } },
    { path: '/helpCenter', component: HelpCenter, meta: { title: '' } },
    { path: '/integral', component: Integral, meta: { title: '' } },
    { path: '/cart', component: Cart, meta: { title: '' } },
    { path: '/commonProblem', component: CommonProblem, meta: { title: '' } },
    { path: '/setup', component: Setup, meta: { title: '' } },
    { path: '/accountBinding', component: AccountBinding, meta: { title: '' } },
    { path: '/sendcode/:type/:phone', component: Sendcode, meta: { title: '' } },
    { path: '/newphone', component: Newphone, meta: { title: '' } },
    { path: '/newpassword', component: Newpassword, meta: { title: '' } },

    //
    { path: '/login', component: Login, meta: { title: '' } },
    { path: '/passwordLogin', component: PasswordLogin, meta: { title: '' } },
    { path: '/forgetPassword', component: ForgetPassword, meta: { title: '' } },
    { path: '/resetPassword/:phone', component: ResetPassword, meta: { title: '' } },
    { path: '/inputCode/:phone', component: InputCode, meta: { title: '' } },
    { path: '/alilogin', component: AliLogin, meta: { title: '' } },
    { path: '/sinalogin', component: SinaLogin, meta: { title: '' } },
    { path: '/wxlogin', component: WxLogin, meta: { title: '' } },

    //
    { path: '/shopDetail', component: ShopDetail, meta: { title: '' } },

    //
    { path: '/certification', component: Certification, meta: { title: '' } },
    { path: '/school', component: School, meta: { title: '' } },
    { path: '/cpeople', component: cPeople, meta: { title: '' } },
    { path: '/addPeople', component: AddPeople },
    { path: '/addPeople/:id', component: AddPeople },
    { path: '/realname', component: Realname, meta: { title: '' } },
    { path: '/realnameSuc', component: RealnameSuc, meta: { title: '' } },
    { path: '/realnameUnbundling', component: RealnameUnbundling, meta: { title: '' } },
    { path: '/face/:orderid', component: Face, meta: { title: '' } },

    //
    { path: '/gohosting', component: Gohosting, meta: { title: '' } },
    { path: '/steps2', component: Steps2, meta: { title: '' } },
    { path: '/fittingsConfirm', component: Fittings, meta: { title: '' } },
    { path: '/uploadimg', component: Uploadimg, meta: { title: '', keepAlive: true, isBack: false } },
    { path: '/gsuccessful', component: Gsuccessful, meta: { title: '' } },

    { path: '/trusteeship', component: Trusteeship, meta: { title: '', keepAlive: true, isBack: false } },
    { path: '/hostDetail/:id', component: HostDetail, meta: { title: '' } },
    { path: '/hostCancel/:id/:number', component: HostCancel, meta: { title: '' } },
    { path: '/sceneDeli/:id', component: SceneDeli, meta: { title: '' } },
    { path: '/platformDeli/:id', component: PlatformDeli, meta: { title: '' } },
    { path: '/postDeli/:id', component: PostDeli, meta: { title: '' } },
    { path: '/hostingDetail/:id', component: HostingDetail, meta: { title: '' } },
    { path: '/hostingExpress/:id', component: HostingExpress, meta: { title: '' } },
    { path: '/nearshop', component: Nearshop, meta: { title: '' } },
    { path: '/notDetail/:id', component: NotDetail, meta: { title: '' } },
    { path: '/notpay/:id', component: NotPay, meta: { title: '' } },

    //
    { path: '/addresslist/:type', component: Addresslist, meta: { title: '' } },
    { path: '/addaddress', component: Addaddress, meta: { title: '' } },
    { path: '/addaddress/:id', component: Addaddress, meta: { title: '' } },

    //
    { path: '/map/:center/:shop', component: Map, meta: { title: '' } },

    { path: '*', component: NotFound, meta: { title: '404-not found' } },

  ],

  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      return { x: 0, y: 0 }
    }
  }

})


