<template>
    <div>
        <div class="box">
            <div class="title">{{detail.model}}</div>
            <div class="detail">
                <div class="hang">
                <span></span>
                <span>{{detail.cate}}</span>
                </div>
                <div class="hang">
                <span></span>
                <span>{{detail.total_price}}</span>
                </div>
                <div class="hang">
                <span></span>
                <span>{{detail.style}}</span>
                </div>
                <div class="hang">
                <span></span>
                <span>{{detail.trust_price}}</span>
                </div>
                <div class="hang" v-if="detail.host_number&&detail.host_number!=''">
                <span></span>
                <span>{{detail.host_number}}</span>
                </div>
            </div>
        </div>
        
        <!--  -->
        <div class="state text-c" v-if="detail.state==''">
          <div></div>
          <div></div>
        </div>
        <div class="state text-c" v-if="detail.state==''">
            <div></div>
            <div></div>
            <div>{{detail.reasons_refusal}}</div>
        </div>

        <div class="pd-lr-15" v-if="detail.state==''"></div>
        <div class="tab_box" v-if="detail.state==''">
            <div class="fc-grey pd-15" v-if="earnings.length==0"></div>
            <div class="flex-jc-between flex-align-items pd-15" v-for="(item,index) in earnings" :key="index">
            <div>
                <div>{{item.rent_time}}</div>
                <div class="time">{{item.rent_start}}{{item.tenancy_term}}</div>
            </div>
            <div>
                <div class="money text-c">+{{item.users_rental}}</div>
                <div class="fc-grey fsz12">{{item.rental}}</div>
            </div>
            </div>
        </div>

        <!--  -->
        <van-popup v-model="showmodel" :close-on-click-overlay="false">
        <div class="text-c position mask_box">
            <div class="flex-jc-center">
            <img src="../../../assets/tg_success.png" class="tg_success">
            <span class="icon" @click="showmodel = false">
                <van-icon name="cross" color="#aeaeae" size="25px"/>
            </span>
            </div>

            <div class="gongxi"></div>
            <div class="gongxi">New bee</div>
            <!-- <div class="zhiyin">
            
            </div> -->
        </div>
        </van-popup>
    </div>
</template>

<script>
import { Toast } from "vant";
export default {
    data() {
        return {
            images: [],
            typenum: 0,
            detail:'',
            showmodel: false,
            active:0,
            earnings:[]
        };
    },
    mounted(){
        this.getdetail()
    },
    methods:{
        getdetail(){
            Toast.loading({ mask: true, message: "..." });
            let postData = this.$qs.stringify({
                no_hardware_id: this.$route.params.id,
                host_number: this.$route.query.hostnumber
            });
            this.axios.post(this.API + "api/Trusteeship/noHardwareDetails", postData)
            .then(res => {
                console.log(res.data, "detail");
                let resdata = res.data;
                if (resdata.code == 200) {
                    Toast.clear();
                    this.detail = resdata.data

                    if(resdata.data.state==''){
                      this.showmodel = true
                    }
                    if(resdata.data.state==''){
                      this.getEarnings()
                    }
                } else {
                    Toast.clear();
                    Toast(resdata.message);
                }
            })
            .catch(error => {
                Toast.clear();
                Toast('')
            });
        },
        getEarnings(){
            Toast.loading({ mask: true, message: "..." });
            let postData = this.$qs.stringify({
                host_number: this.detail.host_number
            });
            // this.axios.post(this.API + "api/Trusteeship/queryEarnings", postData)
            this.axios.post(this.API + "api/Trusteeship/rentalRecord", postData)
            .then(res => {
                console.log(res.data, "earnings");
                let resdata = res.data;
                if (resdata.code == 200) {
                    Toast.clear();
                    this.earnings = resdata.data
                } else {
                    Toast.clear();
                    // Toast(resdata.message);
                }
            })
        },
    }
}
</script>

<style scoped>
.box {
  margin: 15px;
  border-radius: 10px;
  background: rgba(255, 255, 255, 1);
  box-shadow: 0px 0px 18px 0px rgba(188, 188, 188, 0.32);
  overflow: hidden;
}

.title {
  margin: 10px;
  font-size: 16px;
}
.detail {
  padding: 10px;
  color: #383838;
}
.hang {
  line-height: 30px;
}
.state {
  margin: 15px;
  border-radius: 10px;
  background: rgba(255, 255, 255, 1);
  box-shadow: 0px 0px 18px 0px rgba(188, 188, 188, 0.32);
  color: #4ea9f9;
  padding: 30px 0;
}

/*  */
.mask_box {
  width: 240px;
  margin: 25px;
}
.gongxi {
  font-size: 15px;
  height: 25px;
  line-height: 25px;
}
.icon {
  position: absolute;
  top: -10px;
  right: -10px;
}
.tg_success {
  width: 80px;
  height: 85px;
  margin: 10px;
}
.zhiyin {
  color: #aeaeae;
  margin-top: 10px;
}

.tab_box {
  margin: 15px;
  background: rgba(255, 255, 255, 1);
  box-shadow: 0px 0px 18px 0px rgba(188, 188, 188, 0.32);
  border-radius: 10px;
}
.jia {
  color: #4ea9f9;
}
.money {
  color: #4ea9f9;
  font-size: 16px;
}
.fc_money {
  color: #4ea9f9;
  font-size: 50px;
}
.time {
  color: #aeaeae;
  font-size: 12px;
}
.fsz12{
    font-size:12px
}
</style>



