<template>
    <div>
	
        <header class="header bgc mar-b-10 box-sizing">
            <div class="text-c">()</div>
            <div class="text-c num">{{info.users_money||'0'}}</div>
            <div class="text-c marb20"><router-link class="lines" to="/certification"></router-link></div>
            <div class="text-c mar-b-10 fsz12">{{info.order_safe||'-'}}{{info.order_rental||'-'}}{{info.order_total_rent||'-'}}</div>
            <div class="text-c"><span class="fc-red"> {{info.total_price||'-'}}</span></div>
        </header>

        <div class="bgc">
            <div class="border-b pd-15"></div>
            <van-radio-group v-model="radio">
         	
		    	<div v-if='numbuy'  class="flex-jc-between border-b pd-15"  @click="radio ='5'">
                    <div><img src="../../assets/weixin.png" alt="" class="payimg"></div>
                    <van-radio name="5" checked-color="#2DBBF1"></van-radio>
                </div>	
			
			   <div v-else class="flex-jc-between border-b pd-15" @click="radio ='1'">
                    <div><img src="../../assets/weixin.png" alt="" class="payimg"></div>
                    <van-radio name="1" checked-color="#2DBBF1"></van-radio>
                </div>

				
                <div class="flex-jc-between border-b pd-15" @click="radio = '2'">
                    <div><img src="../../assets/ali.png" alt="" class="payimg"></div>
                    <van-radio name="2" checked-color="#2DBBF1"></van-radio>
                </div>
				
                <div class="flex-jc-between pd-15" @click="radio = '3'">
                    <div><img src="../../assets/balance.png" alt="" class="payimg"><span class="fc-red"> {{info.users_balance}}</span></div>
                    <van-radio name="3" checked-color="#2DBBF1"></van-radio>
                </div>
				
            </van-radio-group>
        </div>

        <div class="pd15"><button class="btn text-c" @click="submit"></button></div>

        <van-popup v-model="showWXpay" :close-on-click-overlay='false'>
            <div class="wxbox">
                <p class="text-c border-b"></p>
                <p class="text-c fc-red border-b"><router-link to='/order'></router-link></p>
                <p class="text-c fc-grey" @click="showWXpay = false"></p>
            </div>
        </van-popup>

    </div>
</template>

<script>

import { Toast,Dialog } from "vant";
import { isWeiXin } from "@/utils/util.js";
import wx from "weixin-js-sdk";

export default {
    data(){
        return{
            radio:'3',
            info:'',
            showWXpay:false,
			numbuy:''
        }
    },
	
    created(){
        let wxpaySession = JSON.parse(window.sessionStorage.getItem("wxpaySession"))
        if(wxpaySession){
            if(wxpaySession.orderid==this.$route.params.orderid){
                this.showWXpay = wxpaySession.state
            }
        }
	 console.log("",window.localStorage.getItem("isWeiXin"))
		let isWeiXin = () => {
		   return navigator.userAgent.toLowerCase().indexOf('micromessenger') !== -1
		}
		if (isWeiXin()) {
		  console.log("");
		  this.numbuy = true
		} else {
		  console.log("");
			this.numbuy = false
		}
        this.getfaceRes() 
    },

    mounted(){
        this.getinfo()
    },
    methods:{
	
        getfaceRes() {
            let postData = this.$qs.stringify({
                users_id: JSON.parse(window.localStorage.getItem("userinfo")).users_id,
            });
            this.axios.post(this.API + "api/Order/CheckFace", postData)
            .then(res => {
                console.log(res.data, "getfaceRes");
                let resdata = res.data;
                if (resdata.code == 200) {
                    
                } else {
                    Dialog.alert({
                        message: resdata.message
                    }).then((e) => {
                        this.$router.replace({ path: "/face/"+this.$route.params.orderid })
                    });
                }
            })
            .catch(error => {
                Toast('')
            });
        },
		
		
        getinfo() {
            Toast.loading({ mask: true, message: "..." });
            let postData = this.$qs.stringify({
                users_id: JSON.parse(window.localStorage.getItem("userinfo")).users_id,
                order_id : this.$route.params.orderid
            });
            this.axios.post(this.API + "api/Order/GetPayData", postData)
            .then(res => {
                console.log(res.data, "info");
                let resdata = res.data;
                if (resdata.code == 200) {
                    Toast.clear();
                    this.info = resdata.data;
                } else {
                    Toast.clear();
                    Toast(resdata.message);
                }
            })
            .catch(error => {
                Toast.clear();
                Toast('')
            });
        },
		
     submit(){
           console.log('0001',this.radio)
            if(this.radio==1){
                // Toast('')
                Toast.loading({ mask: true,message: '...',duration:0})
                let postData = this.$qs.stringify({
                    users_id: JSON.parse(window.localStorage.getItem("userinfo")).users_id,
                    order_id : this.$route.params.orderid,
                    pay_way: this.radio
                });
                this.axios.post(this.API + "api/Order/GetPay", postData)
                .then(res => {
                    console.log(res.data, "wxpay");
                    let resdata = res.data
                    if (resdata.code == 200) {
                        Toast.clear()
                        let wxpaySession = {
                            orderid:this.$route.params.orderid,
                            state: true
                        }
                        window.sessionStorage.setItem("wxpaySession", JSON.stringify(wxpaySession))
                        window.location.href = resdata.data
                    } else {
                        Toast.clear()
                        Toast(resdata.message);
                    }
                })
                .catch(error => {
                    Toast.clear()
                    Toast('')
                });
            }
        if(this.radio==5){
            
                Toast.loading({ mask: true,message: '...',duration:0})
                let postData = this.$qs.stringify({
                    users_id: JSON.parse(window.localStorage.getItem("userinfo")).users_id,
                    order_id : this.$route.params.orderid,
                    pay_way: this.radio
                });
                this.axios.post(this.API + "api/Order/GetPay", postData)
                .then(res => {
                    console.log(res, "wxpay");
                    let data = res.data.data
                    if (res.data.code == 200) {
                    Toast.clear()
                   // ()
                     this.wexinPay(
                        {
                          appId: data.appId,
                          nonceStr: data.nonceStr,
                          package: data.package,
                          paySign: data.paySign,
                          signType: data.signType,
                          timeStamp: data.timeStamp
                        },
                      );
                    } else {
                        Toast.clear()
                        Toast(resdata.message);
                    }
                }).catch(error => {
                    Toast.clear()
                    Toast('')
                });
            }
            if(this.radio==2){
                // Toast('')
                if(isWeiXin()){
                    Dialog.alert({
                        message: ''
                    }).then((e) => {

                    });
                    return
                }
                Toast.loading({ mask: true,message: '...',duration:0})
                let postData = this.$qs.stringify({
                    users_id: JSON.parse(window.localStorage.getItem("userinfo")).users_id,
                    order_id : this.$route.params.orderid,
                    pay_way: this.radio
                });
                this.axios.post(this.API + "api/Order/GetPay", postData)
                .then(res => {
                    console.log(res.data, "alipay");
                    window.sessionStorage.removeItem("wxpaySession");
                    Toast.clear()
                    
                    const form = res.data;
                    const div = document.createElement('div');
                    div.id = 'alipay';
                    div.style.opacity='0'
                    div.innerHTML = form;
                    document.body.appendChild(div);
                    document.querySelector('#alipay').children[0].submit(); 
                    // this.$router.replace({
                    //     path: '/applyText',
                    //     query: {html: res.data}
                    // })
                })
                .catch(error => {
                    Toast.clear()
                    Toast('')
                });
            }
            if(this.radio==3){
                Toast.loading({ mask: true,message: '...',duration:0})
                let postData = this.$qs.stringify({
                    users_id: JSON.parse(window.localStorage.getItem("userinfo")).users_id,
                    order_id : this.$route.params.orderid,
                    pay_way: this.radio
                });
                this.axios.post(this.API + "api/Order/GetPay", postData)
                .then(res => {
                    console.log(res.data, "submit");
                    let resdata = res.data;
                    if (resdata.code == 200) {
                        Toast.clear()
                        Dialog.alert({
                            message: ''
                        }).then((e) => {
                           this.$router.replace({ path: "/order" })
                        });
                    } else {
                        Toast.clear()
                        Toast(resdata.message||'');
                    }
                })
                .catch(error => {
                    Toast.clear()
                    Toast('')
                });
            }
        },

      wexinPay(data) {
        //
        let appId = data.appId;
        let timestamp = data.timeStamp;
        let nonceStr = data.nonceStr;
        let signature = data.signature;
        let packages = data.package;
        let paySign = data.paySign;
        let signType = data.signType;
        console.log('')
        wx.config({
            debug: false, // ,apialertpclogpc
            appId: appId, // 
            timestamp: timestamp, // 
            nonceStr: nonceStr, // 
            signature: signature, // 1
            jsApiList: ['chooseWXPay'] // JSJS2
        });
        wx.ready(function () {
            wx.chooseWXPay({
                timestamp: timestamp, // jssdktimestamptimeStampS
                nonceStr: nonceStr, //  32 
                package: packages, // prepay_idprepay_id=***
                signType: signType, // 'SHA1''MD5'
                paySign: paySign, // 
                success: function (res) {
                    // 
					if (res.errMsg == "chooseWXPay:ok") {
				    	Toast.clear()
					    Dialog.alert({
                            message: ''
                        }).then((e) => {
						    window.location.href='https://www.newbee-smart.com/#/order'
                           // this.$router.replace({ path: "/order" })
                        });
					}
                },
				// 
				cancel: function (res) {
					Toast('~')					

				},
				// 
				fail: function (res) {
				  Toast('~')
				}
            });
        });
        wx.error(function (res) {
               Toast('',res)
        });
      },
    }
}
</script>

<style scoped>
.fsz12{
    font-size: 12px
}
.payimg{
    width: 20px;
    height: 20px;
    padding-right: 10px;
    vertical-align: middle;
}
.marb20{
    margin-bottom: 20px;
}
.header {
    padding-top: 10px;
    padding-bottom:10px;
}
.header .num {
    font-size: 40px;
    color: #2ebbf1;
    margin: 15px 0;
}
.header .lines {
    color: #FC8502;
    width: 90px;
    height: 30px;
    display: inline-block;
    text-align: center;
    line-height: 30px;
    box-shadow: 0px 1px 7px 1px #DAD7D7;
    border-radius: 5px;
    font-size: 12px
}

.btn{
    width: 100%;
    display:block;
    height: 42px;
    line-height: 42px;
    border-radius: 20px;
    color: #fff;
    background-image: linear-gradient(90deg, #2DBBF1 0%, #4EA9F9 100%);
}
.pd15{
    padding:15px;
}

.wxbox {
    width: 200px;
}
.wxbox > p {
    padding: 10px 0;
}
</style>



