<template>
  <div>
    <div class="bgc mar-b-10">
      <div class="border-b flex-jc-between header flex-align-items">
        <span></span>
        <div class="time">
          <div class="border text-line" @click="isshow = true">
            {{weektext}}
            <img src="../../assets/icon-triangle.png" class="triangleimg" />
          </div>
          <div class="border">
            <input
              type="number"
              v-model.trim="weekval"
              :disabled="weektext==''?true:false"
              class="bgc"
            />
          </div>
        </div>
      </div>
      <div class="pd-15">
        :
        <span class="fc-red">{{rent}}</span>
      </div>
    </div>

    <div class="bgc">
      <div class="border-b pd-15"></div>
      <van-radio-group v-model="radio">
        <div v-if="numbuy" class="flex-jc-between border-b pd-15" @click="radio = '5'">
          <div class>
            <img src="../../assets/weixin.png" alt="" class="payimg" />
            
          </div>
          <van-radio name="5" checked-color="#2DBBF1"></van-radio>
        </div>

        <div v-else class="flex-jc-between border-b pd-15" @click="radio = '1'">
          <div class>
            <img src="../../assets/weixin.png" alt="" class="payimg" />
            
          </div>
          <van-radio name="1" checked-color="#2DBBF1"></van-radio>
        </div>

        <div class="flex-jc-between border-b pd-15" @click="radio = '2'">
          <div class>
            <img src="../../assets/ali.png" alt="" class="payimg" />
          </div>
          <van-radio name="2" checked-color="#2DBBF1"></van-radio>
        </div>
        <div class="flex-jc-between pd-15" @click="radio = '3'">
          <div class>
            <img src="../../assets/balance.png" alt="" class="payimg" />
            
            <span class="fc-red">{{info.users_balance}}</span>
          </div>
          <van-radio name="3" checked-color="#2DBBF1"></van-radio>
        </div>
      </van-radio-group>
    </div>

    <div class="pd-t-100">
      <div class="btn text-c" @click="submit"></div>
    </div>

    <van-popup v-model="isshow" position="bottom" :close-on-click-overlay="false">
      <van-picker :columns="columns" show-toolbar @cancel="isshow = false" @confirm="onConfirm" />
    </van-popup>

    <van-popup v-model="showWXpay" :close-on-click-overlay="false">
      <div class="wxbox">
        <p class="text-c border-b"></p>
        <p class="text-c fc-red border-b" @click="goback"></p>
        <p class="text-c fc-grey" @click="showWXpay = false"></p>
      </div>
    </van-popup>
  </div>
</template>

<script>
import { Toast, Dialog } from "vant";
import { isWeiXin } from "@/utils/util.js";
import wx from "weixin-js-sdk";

export default {
  data() {
    return {
      radio: "3",
      isshow: false,
      columns: [""],
      weektext: "",
      weekval: "",
      isdisabled: true,
      rent: 0,
      info: "",
      showWXpay: false,
      numbuy: ""
    };
  },
  watch: {
    weekval() {
      if (this.weekval == "") {
        this.rent = 0;
        return;
      }
      if (this.weekval < 0) {
        this.weekval = 1;
        Toast("0");
        return;
      }
      let postData = this.$qs.stringify({
        order_id: this.$route.params.id,
        day: this.weekval
      });
      this.axios
        .post(this.API + "api/Lease_Order/orderRelet", postData)
        .then(res => {
          console.log(res.data, "weekval");
          let resdata = res.data;
          if (resdata.code == 200) {
            this.rent = resdata.data;
          } else {
            Toast(resdata.message);
          }
        });
    }
  },
  created() {
    let wxpayReletSession = JSON.parse(
      window.sessionStorage.getItem("wxpayReletSession")
    );
    if (wxpayReletSession) {
      if (wxpayReletSession.orderid == this.$route.params.id) {
        this.showWXpay = wxpayReletSession.state;
      }
    }
    console.log("", window.localStorage.getItem("isWeiXin"));
    let isWeiXin = () => {
      return navigator.userAgent.toLowerCase().indexOf("micromessenger") !== -1;
    };
    if (isWeiXin()) {
      console.log("");
      this.numbuy = true;
    } else {
      console.log("");
      this.numbuy = false;
    }
    this.getinfo();
  },
  methods: {
    goback() {
      this.$router.go(-1);
    },
    getinfo() {
      let postData = this.$qs.stringify({
        users_id: JSON.parse(window.localStorage.getItem("userinfo")).users_id,
        order_id: this.$route.params.id
      });
      this.axios
        .post(this.API + "api/Order/GetPayData", postData)
        .then(res => {
          console.log(res.data, "info");
          let resdata = res.data;
          if (resdata.code == 200) {
            this.info = resdata.data;
          } else {
            Toast(resdata.message);
          }
        })
        .catch(error => {
          Toast("");
        });
    },

    onConfirm(value, index) {
      console.log(`${value}, ${index}`);
      this.weektext = value;
      this.isshow = false;
    },

    submit() {
      if (this.weekval == "" || this.weektext == "") {
        Toast("");
        return;
      }

      if (this.radio == 1) {
        // Toast("");
        Toast.loading({ mask: true, message: "...", duration: 0 });
        let postData = this.$qs.stringify({
          // users_id: JSON.parse(window.localStorage.getItem("userinfo")).users_id,
          order_id: this.$route.params.id,
          pay_way: this.radio,
          day: this.weekval,
          money: this.rent
        });
        this.axios
          .post(this.API + "api/Lease_Order/orderRenew", postData)
          .then(res => {
            console.log(res.data, "wxpay");
            let resdata = res.data;
            if (resdata.code == 200) {
              Toast.clear();
              let wxpayReletSession = {
                orderid: this.$route.params.id,
                state: true
              };
              window.sessionStorage.setItem(
                "wxpayReletSession",
                JSON.stringify(wxpayReletSession)
              );
              window.location.href = resdata.data;
            } else {
              Toast.clear();
              Toast(resdata.message);
            }
          })
          .catch(error => {
            Toast.clear();
            Toast("");
          });
      }
      if (this.radio == 5) {
        // Toast("");
        Toast.loading({ mask: true, message: "...", duration: 0 });
        let postData = this.$qs.stringify({
          order_id: this.$route.params.id,
          pay_way: this.radio,
          day: this.weekval,
          money: this.rent
        });
        this.axios
          .post(this.API + "api/Lease_Order/orderRenew", postData)
          .then(res => {
            console.log(res, "wxpay");
            let data = res.data.data;
            if (res.data.code == 200) {
              Toast.clear();
              // ()
              this.wexinPay({
                appId: data.appId,
                nonceStr: data.nonceStr,
                package: data.package,
                paySign: data.paySign,
                signType: data.signType,
                timeStamp: data.timeStamp
              });
            } else {
              Toast.clear();
              Toast(resdata.message);
            }
          })
          .catch(error => {
            Toast.clear();
            Toast("");
          });
      }
      if (this.radio == 2) {
        // Toast("");
        if (isWeiXin()) {
          Dialog.alert({
            message: ""
          }).then(e => {});
          return;
        }
        Toast.loading({ mask: true, message: "...", duration: 0 });
        let postData = this.$qs.stringify({
          // users_id: JSON.parse(window.localStorage.getItem("userinfo")).users_id,
          order_id: this.$route.params.id,
          pay_way: this.radio,
          day: this.weekval,
          money: this.rent
        });
        this.axios
          .post(this.API + "api/Lease_Order/orderRenew", postData)
          .then(res => {
            console.log(res.data, "alipay");
            window.sessionStorage.removeItem("wxpayReletSession");
            Toast.clear();
            const form = res.data;
            const div = document.createElement("div");
            div.id = "alipay";
            div.style.opacity = "0";
            div.innerHTML = form;
            document.body.appendChild(div);
            document.querySelector("#alipay").children[0].submit();
          })
          .catch(error => {
            Toast.clear();
            Toast("");
          });
      }
      if (this.radio == 3) {
        Toast.loading({ mask: true, message: "...", duration: 0 });
        let postData = this.$qs.stringify({
          // users_id: JSON.parse(window.localStorage.getItem("userinfo")).users_id,
          order_id: this.$route.params.id,
          pay_way: this.radio,
          day: this.weekval,
          money: this.rent
        });
        this.axios
          .post(this.API + "api/Lease_Order/orderRenew", postData)
          .then(res => {
            console.log(res.data, "submit");
            let resdata = res.data;
            if (resdata.code == 200) {
              Toast.clear();
              Dialog.alert({
                message: ""
              }).then(e => {
                this.$router.replace({ path: "/order" });
              });
            } else {
              Toast.clear();
              Toast(resdata.message || "");
            }
          })
          .catch(error => {
            Toast.clear();
            Toast("");
          });
      }
    },
    wexinPay(data) {
      //
      let appId = data.appId;
      let timestamp = data.timeStamp;
      let nonceStr = data.nonceStr;
      let signature = data.signature;
      let packages = data.package;
      let paySign = data.paySign;
      let signType = data.signType;
      console.log("");
      wx.config({
        debug: false, // ,apialertpclogpc
        appId: appId, // 
        timestamp: timestamp, // 
        nonceStr: nonceStr, // 
        signature: signature, // 1
        jsApiList: ["chooseWXPay"] // JSJS2
      });
      wx.ready(function() {
        wx.chooseWXPay({
          timestamp: timestamp, // jssdktimestamptimeStampS
          nonceStr: nonceStr, //  32 
          package: packages, // prepay_idprepay_id=***
          signType: signType, // 'SHA1''MD5'
          paySign: paySign, // 
          success: function(res) {
            // 
            if (res.errMsg == "chooseWXPay:ok") {
              Toast.clear();
              Dialog.alert({
                message: ""
              }).then(e => {
                window.location.href = "https://www.newbee-smart.com/#/order";
                // this.$router.replace({ path: "/order" })
              });
            }
          },
          // 
          cancel: function(res) {
            Toast("~");
          },
          // 
          fail: function(res) {
            Toast("~");
          }
        });
      });
      wx.error(function(res) {
        Toast("", res);
      });
    }
  }
};
</script>

<style scoped>
.payimg {
  width: 20px;
  height: 20px;
  padding-right: 10px;
  vertical-align: middle;
}
.triangleimg {
  width: 10px;
  height: 6px;
  vertical-align: middle;
}
.header {
  height: 53px;
  padding: 0 15px;
}
.time > div {
  display: inline-block;
  width: 70px;
  height: 28px;
  line-height: 29px;
  border-radius: 5px;
  overflow: hidden;
  text-align: center;
  font-size: 12px;
}
.time > div:nth-of-type(1) {
  margin-right: 5px;
}
.time input {
  width: 100%;
  text-align: center;
}
.pd-t-100 {
  padding: 0 15px;
  padding-top: 100px;
}
.btn {
  height: 42px;
  line-height: 42px;
  border-radius: 20px;
  color: #fff;
  background-image: linear-gradient(90deg, #2dbbf1 0%, #4ea9f9 100%);
}

.wxbox {
  width: 200px;
}
.wxbox > p {
  padding: 10px 0;
}
</style>



