<template>
    <div>
        <div class="pd-15 fc-grey bgc"></div>
        
        <div class="bgc">
        <div class="box flex-jc-between" >
            <div>
                <div class="uploadimg flex-center position" v-if="!fileimg1">
                    <van-uploader :after-read="onRead1" accept="image/png, image/jpeg" multiple>
                        <!-- <img src="../../../assets/tg_camera.png" alt=""> -->
                        <img src="@/assets/sample/sample1.png" alt="">
                        <span class="fsz-12 sampletext"></span>
                    </van-uploader>
                </div>
                <div class="fileimg" v-else>
                    <van-uploader :after-read="onRead1" accept="image/png, image/jpeg" multiple>
                        <img :src="fileimg1.content" alt="">
                    </van-uploader>
                </div>

                <div class="text-c btext"></div>
            </div>
            <div>
                <div class="uploadimg flex-center position" v-if="!fileimg2">
                    <van-uploader :after-read="onRead2" accept="image/png, image/jpeg" multiple>
                        <!-- <img src="../../../assets/tg_camera.png" alt=""> -->
                        <img src="@/assets/sample/sample2.jpeg" alt="">
                        <span class="fsz-12 sampletext"></span>
                    </van-uploader>
                </div>
                <div class="fileimg" v-else>
                    <van-uploader :after-read="onRead2" accept="image/png, image/jpeg" multiple>
                        <img :src="fileimg2.content" alt="">
                    </van-uploader>
                </div>

                <div class="text-c btext"></div>
            </div>
        </div>
        <div class="fc-grey pd-lr-15 bgc fsz12 mar-b-10">()</div>

        <div class="box flex-jc-between">
            <div>
                <div class="uploadimg flex-center position" v-if="!fileimg3">
                    <van-uploader :after-read="onRead3" accept="image/png, image/jpeg" multiple>
                        <!-- <img src="../../../assets/tg_camera.png" alt=""> -->
                        <img src="@/assets/sample/sample3.png" alt="">
                        <span class="fsz-12 sampletext"></span>
                    </van-uploader>
                </div>
                <div class="fileimg" v-else>
                    <van-uploader :after-read="onRead3" accept="image/png, image/jpeg" multiple>
                        <img :src="fileimg3.content" alt="">
                    </van-uploader>
                </div>

                <div class="text-c btext"></div>
            </div>
            <div>
                <div class="uploadimg flex-center position" v-if="!fileimg4">
                    <van-uploader :after-read="onRead4" accept="image/png, image/jpeg" multiple>
                        <!-- <img src="../../../assets/tg_camera.png" alt=""> -->
                        <img src="@/assets/sample/sample4.jpeg" alt="">
                        <span class="fsz-12 sampletext"></span>
                    </van-uploader>
                </div>
                <div class="fileimg" v-else>
                    <van-uploader :after-read="onRead4" accept="image/png, image/jpeg" multiple>
                        <img :src="fileimg4.content" alt="">
                    </van-uploader>
                </div>

                <div class="text-c btext"></div>
            </div>
        </div>
        <div class="fc-grey pd-lr-15 bgc fsz12 pdb">()</div>
        </div>

        <div class="pd-15"><div class="btn text-c" @click="onshowmodel"></div></div>

        <div class="model" v-if="showmodel">
            <div class="main bgc position">
                <div class="closeimg" @click="showmodel=false"><van-icon name="close" /></div>
                <div class="flex-jc-between pd"><span class="fc-grey"></span><span>{{gohostingSession.typetext.cate_name}}</span></div>
                <div class="flex-jc-between pd"><span class="fc-grey"></span><span>{{gohostingSession.brandtext.brand_name}}</span></div>
                <div class="flex-jc-between pd"><span class="fc-grey"></span><span>{{gohostingSession.modeltext.model_name}}</span></div>
                <div class="flex-jc-between pd"><span class="fc-grey"></span><span>{{gohostingSession.colortext}}</span></div>
                <div class="flex-jc-between pd"><span class="fc-grey"></span><span>1</span></div>
                <div class="flex-jc-between pd">
                    <span class="fc-grey"></span>
                    <span class="flex-1 fittings">{{gohostingSession.fittingstring}}</span>
                </div>
                <div class="flex-jc-between pd"><span class="fc-grey"></span><span>{{`${gohostingSession.datetext[0]}-${gohostingSession.datetext[1]}-${gohostingSession.datetext[2]}`}}</span></div>
                <div class="flex-jc-between pd"><span class="fc-grey"></span><span>{{gohostingSession.priceval}}</span></div>
                <div class="flex-jc-between pd"><span class="fc-grey"></span><span>{{gohostingSession.colourtext}}</span></div>
                <div class="flex-jc-between pd"><span class="fc-grey"></span><span>{{gohostingSession.statetext}}</span></div>
                <div class="flex-jc-between pd"><span class="fc-grey"></span><span>{{gohostingSession.serialnumval}}</span></div>
                <div class="flex-jc-between pd"><span class="fc-grey"></span><span>{{gohostingSession.telval}}</span></div>
                <div class="imglist flex-jc-between pd">
                    <img :src="fileimg1.content" alt="">
                    <img :src="fileimg2.content" alt="">
                </div>
                <div class="imglist flex-jc-between pd">
                    <img :src="fileimg3.content" alt="">
                    <img :src="fileimg4?fileimg4.content:''" alt="">
                </div>
                <div class="flex-jc-between pd"><span class="fc-grey"></span><span>30%</span></div>
                <div class="pd">48</div>
                <div class="pd flex-align-items ">
                    <van-checkbox checked-color="#2DBBF1" v-model="isconsent"></van-checkbox>
                    <div class="pdl10"><router-link to="/zagreement/deposit" class="fc-blue"></router-link></div>
                </div>
            </div>

            <div class="btn text-c" @click="submit"></div>
        </div>
    </div>
</template>

<script>
import { Toast } from "vant";
export default {
    beforeRouteEnter(to, from, next) {
        let gohostingSession = JSON.parse(window.sessionStorage.getItem("gohostingSession"));
        if(gohostingSession.isfirst){
            gohostingSession.isfirst = false
            window.sessionStorage.setItem("gohostingSession", JSON.stringify(gohostingSession));
        }else
        if(from.meta.title === ''||from.meta.title === '') {
            to.meta.isBack = true;
        }
        next();
    },
    data(){
        return{
            fileimg1:null,
            fileimg2:null,
            fileimg3:null,
            fileimg4:null,
            isconsent: true,
            showmodel: false,
            gohostingSession: {}
        }
    },
    created(){
        this.isFirstEnter = true;
    },
    methods:{
        onRead1(file) {
            console.log(file)
            if(Array.isArray(file)){
                Toast('') 
                return
            }
            this.fileimg1 = file
        },
        onRead2(file) {
            console.log(file)
            if(Array.isArray(file)){
                Toast('') 
                return
            }
            this.fileimg2 = file
        },
        onRead3(file) {
            console.log(file)
            if(Array.isArray(file)){
                Toast('') 
                return
            }
            this.fileimg3 = file
        },
        onRead4(file) {
            console.log(file)
            if(Array.isArray(file)){
                Toast('') 
                return
            }
            this.fileimg4 = file
        },

        onshowmodel(){
            if(!this.fileimg1||!this.fileimg2||!this.fileimg3){
                Toast("");
                return;
            }
            
            let gohostingSession = JSON.parse(window.sessionStorage.getItem("gohostingSession"));
            console.log(gohostingSession,'gohostingSession') 

            this.gohostingSession = gohostingSession
            this.showmodel = true
        },

        submit(){
            if (!window.localStorage.getItem("userinfo")) {
                this.$router.replace({ path: "/login" });
                return
            }
            if(this.isconsent){
                let { 
                    typetext, //
                    brandtext, //
                    modeltext, //
                    colortext, //

                    datetext ,//
                    colourtext,//
                    colourdes,//
                    statetext,//
                    priceval ,//
                    causetext, //
                    fittingstring, //

                    serialnumval, //
                    telval //
                 } = this.gohostingSession

                let config = {
                    headers:{'Content-Type':'multipart/form-data'}
                }
                Toast.loading({ mask: true,message: '...',duration:0})
                
                let formData = new FormData()
                formData.append('users_id',JSON.parse(window.localStorage.getItem("userinfo")).users_id)
                formData.append('cate',typetext.cate_name)  //
                formData.append('brand',brandtext.brand_name)  //
                formData.append('model',modeltext.model_name)  //
                formData.append('standards',colortext)  //
                formData.append('num',1)  //
                formData.append('buy_time',`${datetext[0]}-${datetext[1]}-${datetext[2]}`)  //
                formData.append('exterior',colourtext)  //
                formData.append('exterior_describe',colourdes)  //
                formData.append('functional_status',statetext)  //
                formData.append('functional_reason',causetext)  //
                formData.append('parts_list',fittingstring)  //
                formData.append('parts_picture',this.fileimg1.file,this.fileimg1.file.name)  //
                formData.append('model_picture',this.fileimg2.file,this.fileimg2.file.name)  // 
                formData.append('phone_picture',this.fileimg3.file,this.fileimg3.file.name)  //
                formData.append('damage_picture',this.fileimg4?this.fileimg4.file:'')  //
                formData.append('serial_number',serialnumval)  //
                formData.append('contact_way',telval)  //
                formData.append('rate','30')  // %
                
                this.axios.post(this.API + "api/Trusteeship/saveTrust",formData,config)
                .then(res => {
                    console.log(res.data, "res")
                    let resdata = res.data
                    if (resdata.code == 200) {
                        Toast.clear()
                        this.$router.replace({ path: '/gsuccessful' })
                    } else {
                        Toast.clear()
                        Toast(resdata.message||'')
                    }
                })
                .catch(error => {
                    Toast.clear()
                    Toast('')
                });
            }else{
                Toast("");
            }
            
        }
    },
    activated() {
        if(!this.$route.meta.isBack || this.isFirstEnter){
            this.fileimg1 = null,
            this.fileimg2 = null,
            this.fileimg3 = null,
            this.fileimg4 = null,
            this.isconsent =  true,
            this.showmodel =  false,
            this.gohostingSession =  {}
        }
        this.$route.meta.isBack=false
        this.isFirstEnter=false;
    },
}
</script>

<style scoped>
.fsz12{
    font-size: 12px;
}
.pdl10{
    padding-left: 10px
}
.pd{
    padding: 10px;
}
.pdb{
    padding-bottom: 10px
}
.btn {
  height: 42px;
  line-height: 42px;
  border-radius: 20px;
  color: #fff;
  background-image: linear-gradient(90deg, #2dbbf1 0%, #4ea9f9 100%);
}
.uploadimg{
    width: 140px;
    height: 100px;
    border-radius: 8px;
    border-bottom: 4px solid #4ea9f9;
    box-shadow: 0px 0px 2px 0px #4ea9f9;
}
.uploadimg img {
    /* width: 43px;
    height: 34px; */
    width: 120px;
    height: 80px;
}
.uploadimg .sampletext {
    position: absolute;
    bottom: 0;
    right: 0;
}
.fileimg img {
    width: 140px;
    height: 100px;
}
.btext{
    font-size: 11px;
    padding: 10px 0;
}
.box{
    padding: 0 15px;
}

.model {
    width: 100%;
    min-height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 6;
    padding: 10px;
    box-sizing: border-box;
}
.model .main {
    border-radius: 5px;
    margin-bottom: 20px;
}
.main .closeimg {
    position: absolute;
    right: 0;
    top: 0;
    z-index: 7;
}
.main > div {
    box-sizing: border-box;
}
.main .imglist > img {
    width: 130px;
    height: 100px;
    border-radius: 5px;
}
.fittings{
    padding-left: 10px;
    display: flex;
    justify-content: flex-end;
}
</style>



